<template>
  <div>
    <template>
      <material-card
        color="accent"
        full-header
      >
        <template #heading>
          <v-tabs
            v-model="tab"
            background-color="transparent"
            slider-color="transparent"
            class="pa-8"
          >
            <v-tab href="#menu-1">
              EXPRESS DRIVER LIST
            </v-tab>
          </v-tabs>
        </template>
      </material-card>
    </template>
    <!-- Table -->
    <v-tabs-items v-model="tab">
      <v-tab-item id="menu-1">
        <v-card flat>
          <v-card-text>
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="itemsWithSno"
              show-select
              :search="search"
              class="elevation-1"
              :loading="itemsWithSno.length == 0"
              loading-text="Loading... Please wait"
            >
              <!-- Header -->
              <template v-slot:top>
                <v-toolbar flat>
                  <v-menu bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        v-bind="attrs"
                        v-on="on"
                      >
                        Actions
                      </v-btn>
                    </template>
                    <v-list>
                      <v-list-item @click="placementDialog = true">
                        <v-list-item-title>
                          Choose Placement
                        </v-list-item-title>
                      </v-list-item>
                      <v-list-item @click="balanceDialog = true">
                        <v-list-item-title>
                          Set Begining Balance
                        </v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                  <v-divider
                    class="mx-4"
                    inset
                    vertical
                  />
                  <v-text-field
                    v-model="search"
                    left
                    class="mx-2"
                    append-icon="mdi-magnify"
                    label="Search"
                    align-right
                  />
                </v-toolbar>
              </template>
              <template v-slot:item.total_order="{ item }">
                {{ item.total_order === null ? 0 : item.total_order }}
              </template>
              <template v-slot:item.village_placement="{ item }">
                <v-chip
                  v-for="(val,i) in item.village_placement"
                  :key="i"
                  small
                >
                  {{ val }}
                </v-chip>
              </template>
              <template v-slot:item.saldo="{ item }">
                {{ formatThousands(item.saldo, { separator: ',' }) }}
              </template>
              <!-- Dialog -->
              <template v-slot:item.actions="{ item }">
                <v-icon
                  small
                  class="mr-0"
                  @click="editItem(item)"
                >
                  mdi-eye
                </v-icon>
              </template>
              <template v-slot:item.online="{ item }">
                <v-chip
                  v-if="item.online == 1"
                  class="ma-2"
                  color="green"
                  text-color="white"
                  x-small
                  @click="changeStatus(item.id)"
                >
                  Online
                </v-chip>
                <v-chip
                  v-else
                  class="ma-2"
                  color="red"
                  text-color="white"
                  x-small
                  @click="changeStatus(item.id)"
                >
                  Offline
                </v-chip>
              </template>
              <template v-slot:no-data>
                <v-btn
                  color="primary"
                  @click="initialize"
                >
                  Reset
                </v-btn>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
    <v-dialog
      v-model="placementDialog"
      max-width="800px"
    >
      <v-card>
        <v-card-title>
          Driver Placement
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-select
              v-model="selectedDistrict"
              :items="district"
              item-text="nama"
              item-value="id"
              label="Select a District"
              @change="filterVillage"
            />
            <!-- <v-col>
                <v-select
                  v-if="filteredVillage.length > 0"
                  v-model="selectedVillage"
                  :items="filteredVillage"
                  multiple
                  chips
                  item-text="nama"
                  item-value="id"
                  label="Select a District"
                />

                <v-select
                  v-else
                  v-model="selectedVillage"
                  disabled
                  :items="filteredVillage"
                  item-text="nama"
                  item-value="id"
                  label="Select a Village"
                />
              </v-col> -->
          </v-form>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn
            color="primary"
            text
            @click="driverPlacement()"
          >
            Save
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="placementDialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Dialog Set Begining Balance -->
    <v-dialog
      v-model="balanceDialog"
      max-width="800px"
    >
      <v-card>
        <v-card-title>
          Begining Balance
        </v-card-title>
        <v-card-text>
          <v-text-field
            v-model="beginBalance"
            type="number"
            clearable
            outlined
            label="Amount"
          />
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn
            color="primary"
            text
            @click="setBalance()"
          >
            Save
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="balanceDialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="dialog"
      transition="dialog-top-transition"
      max-width="400"
      max-height="auto"
    >
      <v-card
        class="rounded-0"
        flat
      >
        <v-toolbar
          color="primary"
          dark
        >
          Data Driver
        </v-toolbar>
        <v-row class="mt-6">
          <v-col style="height:30%;margin-top:-20px">
            <v-img
              width="200px"
              height="200px"
              :src="$baseUrl+editedItem.photo"
            />
          </v-col>
          <v-col style="height:70%;">
            <table>
              <tr>
                <td>Name</td>
                <td>:</td>
                <td>{{ editedItem.name }}</td>
              </tr>
              <tr>
                <td>Email</td>
                <td>:</td>
                <td>{{ editedItem.email }}</td>
              </tr>
              <tr>
                <td>Phone</td>
                <td>:</td>
                <td>{{ editedItem.phone }}</td>
              </tr>
              <tr>
                <td>District</td>
                <td>:</td>
                <td>{{ editedItem.driver_district }}</td>
              </tr>
              <tr>
                <td>Village</td>
                <td>:</td>
                <td>{{ editedItem.driver_village }}</td>
              </tr>
              <!-- <tr>
                              <td>Saldo</td>
                              <td>:</td>
                              <td>{{ editedItem.saldo }}</td>
                            </tr> -->
            </table>
          </v-col>
        </v-row>

        <v-card-actions class="justify-end">
          <v-btn
            text
            color="primary"
            @click="close()"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  var formatThousands = require('format-thousands')

  export default {
    data: () => ({
      search: '',
      formatThousands,
      tab: null,
      dialog: false,
      balanceDialog: false,
      placementDialog: false,
      selectedDistrict: null,
      loading: false,
      beginBalance: 0,
      selectedVillage: [],
      district: [],
      village: [],
      filteredVillage: [],
      sortable: false,
      selected: [],
      headers: [
        {
          text: 'Driver Name',
          align: 'start',
          sortable: true,
          value: 'name',
        },
        { text: 'Email', value: 'email' },
        { text: 'Total Order', value: 'total_order' },
        { text: 'Disctrict Placement', value: 'district_placement' },
        // { text: 'Village Placement', value: 'village_placement' },
        { text: 'Status', value: 'online' },
        { text: 'Actions', value: 'actions', sortable: 'false' },
      ],

      drivers: [],
      editedIndex: -1,
      editedItem: {},
      defaultItem: {
        id: '',
        name: '',
        email: '',
        total_order: '',
        category: '',
        saldo: '',
      },
    }),

    computed: {
      formTitle () {
        return 'Drivers Data'
      },
      itemsWithSno () {
        return this.drivers.map((d, index) => ({ ...d, sno: index + 1 }))
      },

      // villageArr () {
      //   // eslint-disable-next-line camelcase
      //   return this.drivers.map((d, village_placement) => ({ ...d, v: village_placement }))
      // },

    },

    watch: {
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        axios.get('admin/driver-exp').then(res => {
          this.drivers = res.data.driver
          this.district = res.data.district
          this.village = res.data.village
        })
      },
      editItem (item) {
        this.editedIndex = this.drivers.indexOf(item)
        this.editedItem = item
        this.dialog = true
      },

      close () {
        this.dialog = false
      },

      filterVillage () {
        this.filteredVillage = []
        this.filteredVillage = this.village.filter(
          village => village.kecamatan_id === this.selectedDistrict,
        )
      },

      driverPlacement () {
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            district_placement: this.selectedDistrict,
            village_placement: this.selectedVillage,
          }),
        )
        axios
          .post('admin/driver/placement', data)
          .then(() => {
            this.selectedDistrict = null
            this.selectedVillage = null
            this.placementDialog = false
            this.selected = []
            this.initialize()
            this.$swal('Data Updated Successfully', '', 'success')
          })
          .catch(function (error) {
            // handle error
            console.log(error)
          })
      },

      changeStatus (id) {
        axios.get('auth/change-status/' + id).then(() => {
          this.$swal('Data Updated Successfully', '', 'success')
          this.initialize()
        })
      },

      setBalance () {
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            begin_balance: this.beginBalance,
          }),
        )
        axios.post('admin/driver-exp/set-balance', data)
          .then(() => {
            this.$swal('Data Updated Successfully', '', 'success')
            this.balanceDialog = false
            this.beginBalance = 0
            this.selected = []
            this.initialize()
          })
      },
    },
  }
</script>

